<h1 id="服务端" tabindex="-1"><a class="header-anchor" href="#服务端" aria-hidden="true">#</a> 服务端</h1>
<h2 id="企业微信第三方回调协议" tabindex="-1"><a class="header-anchor" href="#企业微信第三方回调协议" aria-hidden="true">#</a> 企业微信第三方回调协议</h2>
<p>SDK 默认会处理事件 <code>suite_ticket</code> ，并会缓存 <code>suite_ticket</code></p>
<blockquote>
<p>{info} 需要注意的是：授权成功、变更授权、取消授权通知时间的响应必须在1000ms内完成，以保证用户安装应用的体验。建议在接收到此事件时 立即回应企业微信，之后再做相关业务的处理。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$server</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">server</span><span class="token punctuation">;</span>

<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">push</span><span class="token punctuation">(</span><span class="token keyword">function</span> <span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
    <span class="token keyword">switch</span> <span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'InfoType'</span><span class="token punctuation">]</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
        <span class="token comment">//推送suite_ticket</span>
        <span class="token keyword">case</span> <span class="token string single-quoted-string">'suite_ticket'</span><span class="token punctuation">:</span>
            <span class="token keyword">break</span><span class="token punctuation">;</span>
        <span class="token comment">//授权成功通知</span>
        <span class="token keyword">case</span> <span class="token string single-quoted-string">'create_auth'</span><span class="token punctuation">:</span>
            <span class="token keyword">break</span><span class="token punctuation">;</span>
        <span class="token comment">//变更授权通知</span>
        <span class="token keyword">case</span> <span class="token string single-quoted-string">'cancel_auth'</span><span class="token punctuation">:</span>
            <span class="token keyword">break</span><span class="token punctuation">;</span>
        <span class="token comment">//通讯录事件通知</span>
        <span class="token keyword">case</span> <span class="token string single-quoted-string">'change_contact'</span><span class="token punctuation">:</span>
            <span class="token keyword">switch</span> <span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'ChangeType'</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                <span class="token keyword">case</span> <span class="token string single-quoted-string">'create_user'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'新增成员事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                <span class="token keyword">case</span> <span class="token string single-quoted-string">'update_user'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'更新成员事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                 <span class="token keyword">case</span> <span class="token string single-quoted-string">'delete_user'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'删除成员事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                 <span class="token keyword">case</span> <span class="token string single-quoted-string">'create_party'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'新增部门事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                 <span class="token keyword">case</span> <span class="token string single-quoted-string">'update_party'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'更新部门事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                 <span class="token keyword">case</span> <span class="token string single-quoted-string">'delete_party'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'删除部门事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
                 <span class="token keyword">case</span> <span class="token string single-quoted-string">'update_tag'</span><span class="token punctuation">:</span>
                    <span class="token keyword">return</span> <span class="token string single-quoted-string">'标签成员变更事件'</span><span class="token punctuation">;</span>
                    <span class="token keyword">break</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
            <span class="token keyword">break</span><span class="token punctuation">;</span>
        <span class="token keyword">default</span><span class="token punctuation">:</span>
            <span class="token keyword">return</span> <span class="token string single-quoted-string">'fail'</span><span class="token punctuation">;</span>
            <span class="token keyword">break</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$server</span><span class="token operator">-></span><span class="token function">serve</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$response</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br></div></div>